<template>
  <div class="app-container">
    <h1>æå­è¯å«</h1>
    <p>{{text}}</p>
    <el-button type="primary" @click="recognizEN">recognizEN</el-button>
    <el-button type="primary" @click="recognizeCh">recognizeCh</el-button>
    <br />
    <img id="img-en" alt="Vue logo" src="./en.png" />
    <img id="img-ch" alt="Vue logo" src="./chine.png" />
  </div>
</template>
<script>
import { createWorker, PSM, OEM } from "tesseract.js";

const worker = createWorker({
  logger: m => console.log(m)
});

export default {
  data() {
    return {
      text: ""
    };
  },
  mounted() {},
  methods: {
    async recognizEN() {
      const loading = this.$loading({
        lock: true,
        text: "çæä¸­â¦â¦",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      try {
        const img = document.getElementById("img-en");
        console.log(img);
        await worker.load();
        await worker.loadLanguage("eng");
        await worker.initialize("eng", OEM.LSTM_ONLY);
        await worker.setParameters({
          tessedit_pageseg_mode: PSM.SINGLE_BLOCK
        });
        const {
          data: { text }
        } = await worker.recognize(img);
        console.log(text);
        this.text = text;
        loading.close();
      } catch (err) {
        console.error(err);
        loading.close();
      }
    },
    async recognizeCh() {
      const loading = this.$loading({
        lock: true,
        text: "çæä¸­â¦â¦",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)"
      });
      try {
        const img = document.getElementById("img-ch");
        console.log(img);
        await worker.load();
        await worker.loadLanguage("chi_sim");
        await worker.initialize("chi_sim");
        const {
          data: { text }
        } = await worker.recognize(img);
        console.log(text);
        this.text = JSON.stringify(text).replace(/\\n/g, "\\r");
        loading.close();
      } catch (err) {
        console.error(err);
        loading.close();
      }
    }
  }
};
</script>